import React, { Component } from 'react';
import { connect } from 'react-redux';

import styles from './SquareCard.module.css';

class SquareCard extends Component {

  routeToExternal = (e, url) => {
    window.open(url, '_blank');
  }

  render() {

    return (
      <div className={styles.Box}>
        <div>
          <img onClick={(e) => {this.routeToExternal(e, this.props.url)}} src={this.props.img} alt="Project Logo"></img>
        </div>
        <div className={styles.Content}>

          <p>{this.props.title}</p>

        </div>
      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
  };
};

export default connect(mapStateToProps)(SquareCard);
