import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavHashLink as NavLink } from 'react-router-hash-link';
import styles from './NavBar.module.css';

class NavBar extends Component {
  
  render () {
    const scrollWithOffset = (el, offset) => {
      const elementPosition = el.offsetTop - offset;
      window.scroll({
        top: elementPosition,
        left: 0,
        behavior: "smooth"
      });
    }

    return (
      <div className={styles.Background}>
        <div className={styles.Content}>
          <div className={styles.NavItemsGroup}>

            <NavLink smooth activeClassName={styles.ActiveTab} to={'/#Home'} exact>
              <div className={styles.NavItem}>Home</div>
            </NavLink>
            
            <NavLink smooth scroll={el => scrollWithOffset(el, 300)} activeClassName={styles.ActiveTab} to='/#OurCollection'>
              <div className={styles.NavItem}>Our Collection</div>
            </NavLink>

            <NavLink smooth activeClassName={styles.ActiveTab} to={'/#AboutUs'}>
              <div className={styles.NavItem}>About Us</div>
            </NavLink>

            <NavLink smooth activeClassName={styles.ActiveTab} to={'/#ContactUs'}>
              <div className={styles.NavItem}>Contact</div>
            </NavLink>
          </div>

          <div className={styles.Burger} onClick={this.props.toggleDrawer}>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
  };
};

export default connect( mapStateToProps, null, null, {pure: false} )( NavBar );