import React, { Component } from 'react';
import { connect } from 'react-redux';

import styles from './Project.module.css';

class Project extends Component {

  routeToExternal = (e, url) => {
    window.open(url, '_blank');
  }

  render() {

    return (
      <div className={styles.Box}>
        <div>
          <img onClick={(e) => {this.routeToExternal(e, this.props.url)}} src={this.props.img} alt="Project Logo"></img>
          <div class={styles.BottomModal}>
            <p class={styles.BottomModalText}>AVAILABLE IN MORE OPTIONS</p>
          </div>
        </div>
        <div className={styles.Content}>

          <p>{this.props.title}</p>

          <div className={styles.Price}>
            <p>${this.props.price}</p>
            <img src="https://wixlabs-amazon.appspot.com/assets/images/primelogo.svg?fbclid=IwAR26rBrLTh99zjEGhGiYCNZ5d7Fyd01Bzp9g2v4CsmLJcG7TgxkFvsXOjxA" alt="Amazon Prime Logo" className={styles.PrimeLogo} onClick={(e) => {this.routeToExternal(e, this.props.url)}}/>
          </div>

          <button className={styles.ViewProject} onClick={(e) => {this.routeToExternal(e, this.props.url)}}>Buy now on Amazon</button>
        </div>
      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
  };
};

export default connect(mapStateToProps)(Project);
